import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';
import {useParams} from 'react-router';
import { getPostById, editPostById } from './request';
import { TextField, Button , Box, Stack} from '@mui/material';
import { useNavigate } from 'react-router';

const EditPost = ({accessToken}) => {
    const [content, setContent] = useState('');
    const [title, setTitle] = useState('');
    const navigate = useNavigate();

    const params = useParams();

    useEffect(()=>{
        (async ()=>{
            const post = await getPostById({ _id: params._id })
            setContent(post.content);
            setTitle(post.title);
        })();
    }, []);

    const onTitleChange = (e) => {
        setTitle(e.target.value);
    }

    const onContentChange = (e) => {
        setContent(e.target.value);
    }

    const onSubmit = async () => {
        await editPostById({ _id: params._id, accessToken, content, title });
        navigate('/admin');
    }

    const onSave = async () => {
        await editPostById({ _id: params._id, accessToken, content, title });
    }

    return <Box sx={{ p: 2, border: '1px dashed grey', justifyContent: 'center' }} className='post-editor'>
        <Stack spacing={2}>
            <TextField label="Title" value={title} onChange={onTitleChange}></TextField>
            <TextField value={content} onChange={onContentChange} className="post-content" multiline rows={15}></TextField>
            <Button onClick={onSave} variant="contained">Save</Button>
            <Button onClick={onSubmit} variant="contained">Post</Button>
    </Stack>
    </Box>;

};

const mapStateToProps = (state) => ({
    accessToken : state.user.accessToken
});

export default connect(mapStateToProps)(EditPost);
