import React, { useState } from 'react';
import { sendPost } from './request';
import {Button, TextField, Box, Stack} from '@mui/material';
import { connect } from 'react-redux';
import { useNavigate } from 'react-router';

const NewPost = ({accessToken}) => {
    const [content, setContent] = useState('');
    const [title, setTitle] = useState('');
    const navigate = useNavigate();

    const onTitleChange = (e) => {
        setTitle(e.target.value);
    }

    const onContentChange = (e) => {
         setContent(e.target.value);
    }
    
    const onSubmit = ()=>{
        sendPost({accessToken, content, title});
        navigate('/admin');
    }  

    return <Box sx={{ p: 2, border: '1px dashed grey', justifyContent: 'center' }} className='post-editor'>
        <Stack spacing={2}>
        <TextField label="Title" value={title} onChange={onTitleChange}></TextField>
        <TextField value={content} onChange={onContentChange} multiline rows={15}></TextField>
        <Button onClick={onSubmit} variant="contained">Post</Button>
        </Stack></Box>;
};

const mapStateToProps = (state) => ({
    accessToken : state.user.accessToken
});

export default connect(mapStateToProps)(NewPost);
